




















































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import CButton from '@/components/CButton.vue';
import CombinationLister from '@/typescript/CombinationLister.ts';

@Component({
  components: {
    CButton,
  },
  head: {
    title: {
      inner: '組み合わせの総数',
    },
  },
})
export default class CombinationCalc extends Vue {
  // Data
  private dataButtonText: string = '計算';
  private dataN: number = 0;
  private dataR: number = 0;
  private dataNKey: number = 0;
  private dataRKey: number = 1;
  private dataCount: number = 0;

  // Lifecycle hook
  private created(): void {
    this.dataNKey = Math.random() * 1;
    this.dataRKey = Math.random() * 2;
  }

  // Computed
  private get numN(): number | string {
    if (0 < this.dataN) {
      return this.dataN;
    } else {
      return '';
    }
  }
  private get numR(): number | string {
    if (0 < this.dataR) {
      return this.dataR;
    } else {
      return '';
    }
  }
  private get count(): number | string {
    if (0 < this.dataCount) {
      return this.dataCount;
    } else {
      return '';
    }
  }

  @Watch('dataN')
  private watchOnNChanged(newValue: string, orgValue: string): void {
    this.arrangeR();
  }
  @Watch('dataR')
  private watchOnRChanged(newValue: string, orgValue: string): void {
    this.arrangeR();
  }

  // Methods
  private listenOnNInputed($event: Event): void {
    let number: number | string = (<HTMLInputElement>(<any>$event).target).value;
    if (number.match(/^([0-9]|[1-9][0-9]+)$/g)) {
      this.dataN = Number(number);
    } else {
      this.dataN = 0;
    }
  }
  private listenOnRInputed($event: Event): void {
    let number: number | string = (<HTMLInputElement>(<any>$event).target).value;
    if (number.match(/^([0-9]|[1-9][0-9]+)$/g)) {
      this.dataR = Number(number);
    } else {
      this.dataR = 0;
    }
  }
  private arrangeR(): void {
    if (this.dataN < this.dataR) {
      this.dataR = this.dataN;
    }
  }
  private calc(): void {
    this.dataCount = CombinationLister.calc(this.dataN, this.dataR);
  }
}
