export default class CombinationLister {
  private static _combinations: string[][] = [];
  private static _specifiedCount: number = -1;

  public static generateCombinations(
    items: string[], 
    specifiedCount: number = CombinationLister._specifiedCount
  ): string[][] {
    CombinationLister._combinations.length = 0;
    CombinationLister._specifiedCount = specifiedCount;
    if (CombinationLister._specifiedCount < 0) {
      CombinationLister._specifiedCount = 1;
    }
    if (0 < CombinationLister._specifiedCount) {
      let partialItems: string[] = [];
      this.generateCombinationsRecursively(items, partialItems);
    }
    return CombinationLister._combinations;
  }

  private static generateCombinationsRecursively(items: string[], partialItems: string[]): void {
    let storedPartialItems: string[] = [];
    for (let i: number = 0; i < items.length; i++) {
      storedPartialItems = partialItems.concat(items[i]);
      if (storedPartialItems.length < CombinationLister._specifiedCount) {
        let slicedItems: string[] = [];
        // Different from class "PermutationCalc"
        slicedItems = slicedItems.concat(items.slice(i + 1));
        this.generateCombinationsRecursively(slicedItems, storedPartialItems);
      } else {
        if (!CombinationLister._combinations.some((permutation) => {
          if (JSON.stringify(permutation) === JSON.stringify(storedPartialItems)) {
            return permutation;
          }
        })) {
          CombinationLister._combinations.splice(CombinationLister._combinations.length, 0, storedPartialItems);
        }
      }
    }
  }

  public static calc(numN: number, numR: number): number {
    let answer: number = 0;
    if (0 < numN && 0 < numR) {
      let aliasSmallR: number = numN - numR < numR
        ? numN - numR
        : numR;
      answer = 1;
      for (let i: number = numN - aliasSmallR + 1; i <= numN; i++) {
        answer *= (i / aliasSmallR);
        aliasSmallR--;
      }
    }
    return answer;
  }
}